import React, { useEffect, useContext } from 'react'
import GameContext from './../../contexts/game'
import Die from './../Die'
import './style.scss'

const DiceSet = () => {
  const {
    rolling,
    setRolling,
    dice,
    setDice,
    players,
    currentPlayerIndex,
    updateLog,
    rollIndex,
    turns,
    turnPlayed,
    refs
  } = useContext(GameContext)
  const diceValues = dice.map(d => d.value)

  useEffect(() => {
    updateLog(`${players[currentPlayerIndex]} rolled ${diceValues.join(', ')}`)
    setRolling(true)
    setTimeout(() => {
      setRolling(false)
    }, 500)
  }, [rollIndex, currentPlayerIndex, turns]) //eslint-disable-line

  const onDieClick = (i) => {
    if (turnPlayed) {
      return
    }
    const nextDice = [...dice]
    nextDice[i].saved = !nextDice[i].saved
    setDice(nextDice)
  }

  return (
    <div className="Dice">
      {dice.map((die, i) => <Die
        refRef={refs[i]}
        key={i}
        value={die.value}
        saved={die.saved}
        rolling={rolling}
        onClick={() => {onDieClick(i)}}
      />)}
    </div>
  )
}

export default DiceSet